.modal-wrapper{
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.651);
    display: none;
    align-items: center;
    justify-content: center;
}
.modal{
  
    background: #fff;
    padding: 40px;
    border-radius: 10px;
    font-size: 30px;
    max-width: 400px;
    width: 100%;
    box-sizing: border-box;
}
.modal h2{
    text-align: center;
    margin: 0;
    margin-bottom: 20px;
    padding: 0;
}
.button{
    display: flex;
    align-items: center;
    justify-content: center;
    color: transparent;
    border: 1px solid #000;
    cursor: pointer;
    color: #000;
    max-width: 200px;
    width: 100%;
    height: 80px;
    font-size: 30px;
    margin: 0 auto;
}
.block{
    display: flex;
}
@media(max-width:700px){
    .modal-wrapper{
        padding: 0 10px;
    }
}