.row{
    display: flex;
    justify-content: space-between;
    align-items: center;
width: 100%;
}
.row-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    max-width: 300px;
    width: 100%;
    height: 300px;
}

.GameField{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
}
.GameField p{
    font-size: 30px;
}