.congrats-wrapper{
    position: fixed;
    z-index: 9;
    display: none;
    width: 100%;
    height: 100%;
}
.congrats-wrapper img{
    position: fixed;
}
.congrats-wrapper img:nth-child(1){
    top: 120px;
    left: 500px;
}
.congrats-wrapper img:nth-child(2){
    top:200px;
    right: 500px;
}
.congrats-wrapper img:nth-child(3){
    top: 500px;
    left: 700px;
}
.block{
    display: flex;
}
@media(max-width:700px){
    .congrats-wrapper img:nth-child(1){
        top: 80px;
        left: 200px;
    }
    .congrats-wrapper img:nth-child(2){
      top: auto;
        bottom:100px;
        right: 50px;
    }
    .congrats-wrapper img:nth-child(3){
        top: 300px;
        left: 0px;
    }
}